.container {
  font-size: 40px;
  background-size: cover;
}
.para {
  position: absolute;
  text-align: start;
  top: 200px;
  z-index: 0;

  margin: 100px 0px 80px 10px;
}
.header {
  margin: 0px 0px 0px 0px;
  color: #000000;
  position: absolute;
  top: 0px;
  justify-content: start;
  z-index: 0;
}

.buttons {
  margin: 300px 100px 100px 100px;
  position: absolute;
  top: 200px;
  z-index: 0;
}

.landing-image {
  position: relative;
  width: 900px;
  height: 750px;
  left: 460px;
  top: 0px;
  z-index: -1;
}



@media only screen and (max-width: 900px) {
  .container {
    height: 100vh;
    margin-top: 10px;
    background-repeat: no-repeat;
    align-items: flex-start;
  }
  .header {
    color: #292964;
    font-size: 100;
    z-index: 0;
  }
  .buttons {
    margin: 300px 100px 0px 100px;
    position: absolute;
    top: 300px;
    z-index: 0;
  }
}
