@media only screen and (max-width: 900px) {
  .toggle {
    display: none;
  }
  .nav_btn {
    display: block;
  }
  .para {
    display: none;
  }
  .landing,
  .about,
  .gallery,
  .modulelist,
  .contact,
  .signin,
  .footer,
  .tutoring {
    display: flex;
    height: 50%;
    -ms-text-size-adjust: 0.2rem;
    align-items: center;
    justify-content: center;
    z-index: 0;
    width: "100%";
    /* spectratio: 10 / 3; */
    padding: "8rem";
  }
}

.navbar{
    position: fixed;
    top: 0;
    width: 100%;
    background:linear-gradient(to left,#d65d3f 0%, #8C92AC 100%);
    height:50px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    z-index: 1;
    }
    
    .menu-bars{
        margin-left: 2rem;
        font-size:3rem;
        background:none;
    }
    .ul{
        -webkit-padding-start:0px;
                padding-inline-start:0px;
    }
    .vert-align {
        top:0;
        position: fixed;
        z-index:1;
    }
    
  
    .nav-menu{
        background: linear-gradient(to left, #d65d3f 0%, #8c92ac 100%);
      width:250px;
      height :100vh;
      display: flex;
      justify-content: center;
      position: fixed;
      top:0;
      left:-100%;
      transition:850ms;
      z-index: 1;
    
  }
  .nav-menu.active{
       left:0;
       transition: 450ms;
  }
  
  .nav-text{
      display: flex;
      justify-content:flex-start;
      align-items: center ;
      padding: 8px, 0px, 8px, 16px;
      list-style:none ;
      height: 50px;
  }
  .nav-text a{
      text-decoration: none;
      color:black;
      font-size: 15px;
      width:95%;
      height:100%;
      display: flex;
      align-items: center;
      padding:0 16px;
      border-radius: 4px;
  }
  
  .nav-text a:hover{
      background-color: #536170;
  
  }
  .nav-menu-items{
      height: 100%;
      width:100%
  }
  .navbar-toggle{
    background: linear-gradient(to left, #d65d3f 0%, #8c92ac 100%);
      width: 100%;
      height :80px;
      display: flex;
      justify-content: flex-start;
      align-items: center;
  
  }
  
  .span{
      margin-left:16px;
  }
  
