.back-to-top {
  position: fixed;

  right: 40px;
  font-size: 16px;
  width: auto;
  cursor: pointer;
  bottom: 8px;
  border-radius: 100px;
}

.back-to-top:hover {
  background: #a3197a;
 
  transform: scale(1.3);
}

.footer {
  position: fixed;
  bottom: 0px;
  right: 0px;
  width: 100%;
  background: linear-gradient(to left, #d65d3f 0%, #8c92ac 100%);

  height: 40px;
}
.footer-head {
  position: absolute;
  left: 40px;
  right: 62.34%;
  top: 30.17%;
  bottom: 38.79%;

  /* Heading 4 */

  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 36px;
  /* identical to box height, or 150% */
}
