.App{
  height: 3110px;
}
.toggle {
  display: block;
}
.nav_btn {
  display: none;
}

.login{
  z-index: 1;

}


.table {
  width: 500px;
  font-size: 100px;
}

.card {
  margin: 10px 10px 0 0;
  height: 200px;
}

.logo {
  align-items: flex-end;
  color: black;
}
.logo:hover {
  cursor: pointer;
  color: rgb(255, 255, 255);
}

@media only screen and (max-width: 900px) {
  .toggle {
    display: none;
  }
  .nav_btn {
    display: block;
  }
  .para {
    display: none;
  }
  .landing,
  .about,
  .gallery,
  .modulelist,
  .contact,
  .signin,
  .footer,
  .tutoring {
    display: flex;
    height: 50%;
    -ms-text-size-adjust: 0.2rem;
    align-items: center;
    justify-content: center;
    z-index: 0;
    width: "100%";
    /* spectratio: 10 / 3; */
    padding: "8rem";
  }
}
